/* styles/RegisterPage.css */

.register-page {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(135deg, #06b6d4, #3b82f6);
  padding: 20px;
}

.register-page form {
  background: #fff;
  padding: 2rem;
  border-radius: 16px;
  width: 100%;
  max-width: 420px;
  box-shadow: 0px 6px 20px rgba(0, 0, 0, 0.1);
  animation: fadeIn 0.6s ease-in-out;
}

.register-page h1 {
  text-align: center;
  margin-bottom: 1.5rem;
  font-size: 1.8rem;
  font-weight: 600;
  color: #1f2937;
}

.register-page .ant-form-item-label > label {
  font-weight: 500;
  color: #374151;
}

.register-page input {
  border-radius: 8px;
  padding: 10px;
  border: 1px solid #d1d5db;
  transition: border-color 0.3s;
}

.register-page input:focus {
  border-color: #06b6d4;
  outline: none;
}

.register-page button {
  width: 100%;
  margin-top: 10px;
  background: #06b6d4;
  border: none;
  border-radius: 8px;
  font-weight: 500;
  padding: 10px;
  transition: all 0.3s ease;
}

.register-page button:hover {
  background: #0891b2;
  transform: translateY(-2px);
}

.register-page a {
  color: #0ea5e9;
  font-size: 0.9rem;
  transition: color 0.3s;
}

.register-page a:hover {
  color: #0284c7;
}

@keyframes fadeIn {
  from { opacity: 0; transform: translateY(20px); }
  to { opacity: 1; transform: translateY(0); }
}
